.App {
  text-align: center;
  display: flex;
  min-width: 100vw;
  background-color: transparent;
  background-image: linear-gradient(135deg, #111 0%, #666 100%);
  background-position: 0%;
  background-size: auto;
  background-repeat: repeat;
  background-attachment: scroll;
  background-origin: padding-box;
  background-clip: border-box;
  background-position: 0% 0%;
  background-size: auto auto;
  background-repeat: repeat;
  background-attachment: scroll;
  background-origin: padding-box;
  background-clip: border-box;
  max-width: 100vw;
  transition: 0.1s ease-in-out;
  flex-direction: column;
  align-items: start;
  justify-items: start;
  align-content: center;
  justify-content: center;
  align-self: center;
  overflow: hidden;
  color: white;
  min-height: 100vh;
  margin: 0px;
  padding: 0px;
  max-height: 100vh;
}

.someGames {
  margin: 0px;
  padding: 0px;
  text-align: center;
  width: 100%;
  font-size: 2vh;
}

.ios-peek {
  position: fixed;
  width: 100%;
  height: 100%;
  bottom: 0px;
  z-index: -1;
  background-color: transparent;
  background-image: linear-gradient(135deg, #111 0%, #666 100%);
  background-position: 0%;
  background-size: auto;
  background-repeat: repeat;
  background-attachment: scroll;
  background-origin: padding-box;
  background-clip: border-box;
}